.cartBox{
width: 100%;
margin-bottom: 15px;
}
.cartBox .shop_info{
position: relative;
width: 100%;
height: 38px;
background-color: #fff;
line-height: 38px;
vertical-align: baseline;
}
.cartBox .shop_info .all_check{
position: relative;
float: left;
width: 30px;
height: 38px;
}
.cartBox .shop_info .all_check input[type="checkbox"]{
position: absolute;
z-index: 0;
left: -20px;
top: -20px;
}
.cartBox .shop_info .all_check .shop{
position: absolute;
top:13px;
}
.cartBox .shop_info .shop_name{
float: left;
}
/*商品列表*/
.cartBox .order_content{
border: 1px solid #ccc;
}
.cartBox .order_content a{
display: block;
}
.order_charge{
width: 100%;
height: 40px;
border-bottom: 1px solid #e7e7e7;
}
.order_charge p{
display: inline-block;
line-height: 40px;
}
.order_charge p.charge_info{
margin:0 30px 0 50px;
}
.order_charge p.charge_info span{
border: 1px solid #f60;
padding:2px;
color: #f60;
}
.order_lists{
width: 100%;
height: 130px;
border-bottom: 1px solid #e7e7e7;
}
.order_lists:last-child{
border-bottom: none;
}
.order_lists li{
float: left;
height: 100%;
}
.order_lists .list_chk{
position: relative;
width: 50px;
}
.order_lists .list_chk input[type="checkbox"]{
position: absolute;
z-index: 0;
left: -20px;
top: -20px;
}
.order_lists .list_chk label{
margin: 20px 0 0 24px;
}
.order_lists .list_con{
width: 342px;
}
.order_lists .list_con .list_img{
width: 80px;
height: 80px;
margin-top: 20px;
float: left;
}
.order_lists .list_con .list_img img{
width: 100%;
vertical-align: top;
}
.order_lists .list_con .list_text{
margin: 20px 0 0 10px;
line-height: 18px;
width: 200px;
float: left;
}
.order_lists .list_con .list_text a{
color: #3c3c3c;
margin-bottom: 5px;
}
.order_lists .list_con .list_text a:hover{
color: #ff873e;
text-decoration: underline;
}
.order_lists .list_con .list_text .list_custom{
background-color:#ff8000;
padding:3px;
border-radius: 3px;
color: #fff;
}
.order_lists .list_info{
width: 252px;
-webkit-box-sizing: border-box;
        box-sizing: border-box;
padding: 20px 0;
}
.order_lists .list_info p{
color: #9c9c9c;
line-height: 18px;
margin-left: 15px;
}
.order_lists .list_price{
width: 130px;
}
.order_lists .list_price .price{
margin-top: 20px;
line-height: 18px;
font-family: Verdana,Tahoma,arial;
color: #3c3c3c;
font-weight: bold;
}
.order_lists .list_price .charge{
position: relative;
z-index: 4
}
.order_lists .list_price .charge p{
color: #ff0000;
border:1px solid #ff0000;
width: 50px;
height:15px;
line-height: 15px;
padding:3px;
}
.order_lists .list_price .charge .chargebox{
display: none;
width: 300px;
height: 110px;
background: #000;
z-index: 1;
}
.order_lists .list_amount{
width: 120px;
}
.order_lists .list_amount .amount_box{
margin-top: 20px;
width: 77px;
height: 25px;
position: relative;
}
.order_lists .list_amount .amount_box input{
width: 39px;
height: 15px;
line-height: 15px;
border: 1px solid #aaa;
color: #343434;
text-align: center;
padding: 4px 0;
background-color: #fff;
z-index: 2;
position: absolute;
left: 18px;
float: left;
}
.order_lists .list_amount .amount_box a{
float: left;
height: 23px;
width: 17px;
border: 1px solid #e5e5e5;
background: #f0f0f0;
text-align: center;
line-height: 23px;
color: #444;
position: absolute;
top:0;
}
.order_lists .list_amount .amount_box a:hover{
border-color: #ff873e;
text-decoration: none;
color: #ff873e;
z-index: 3;
}
.order_lists .list_amount .amount_box .reduce{
left: 0;
}
.order_lists .list_amount .amount_box .reSty{
color: #cbcbcb;
}
.order_lists .list_amount .amount_box .reSty:hover{
border-right: none;
border-color: #e5e5e5;
text-decoration: none;
color: #cbcbcb;
}
.order_lists .list_amount .amount_box .plus{
border-left-color: transparent;
right: 0;
}
.order_lists .list_sum{
width: 140px;
}
.order_lists .list_sum .sum_price{
line-height: 18px;
margin-top: 20px;
font-family: Verdana,Tahoma,arial;
color: #900;
font-weight: bold;
}
.order_lists .list_op{
width: 164px;
}
.order_lists .list_op .collect{
margin-top: 20px;
}
.order_lists .list_op .del{
line-height: 18px;
margin:5px 0;
}
.order_lists .list_op .custombox{
width: 200px;
height: 350px;
background-color: #000;
display: none;
position: relative;
z-index: 9
}
/*底部总计算价*/
.bar-wrapper{
width: 1200px;
height: 50px;
position: fixed;
bottom: -1px;
z-index: 99;
background: #e5e5e5;
}
.bar-wrapper .bar-right{
float: right;
color: #3c3c3c;
}
.bar-wrapper .bar-right strong{
color: #900;
}
.bar-wrapper .bar-right .piece{
float: left;
min-width: 110px;
margin-right: 20px;
height: 50px;
line-height: 50px;
}
.bar-wrapper .bar-right .piece .piece_num{
display: inline-block;
padding: 0 10px;
font-weight: 700;
font-size: 18px;
font-family: tohoma,arial;
}
.bar-wrapper .bar-right .totalMoney{
float: left;
min-width: 100px;
height: 50px;
line-height: 50px;
}
.bar-wrapper .bar-right .totalMoney .total_text{
float: right;
font-weight: 400;
font-size: 20px;
font-family: Arial;
vertical-align: middle;
margin-right: 10px;
margin-left: 15px;
}
.bar-wrapper .bar-right .calBtn{
float: left;
}
.bar-wrapper .bar-right .calBtn a{
display: block;
width: 120px;
height: 50px;
color: #fff;
background: #B0B0B0;
cursor: not-allowed;
font-size: 22px;
letter-spacing: 5px;
text-decoration: none;
line-height: 50px;
text-align: center;
border-radius: 2px;
}
.bar-wrapper .bar-right .calBtn a.btn_sty{
background: #ff873e;
cursor: pointer;
}
/*自己定义的模态框*/
.model_bg{
position: absolute;
top:0;
left: 0;
bottom: 0;
right: 0;
background: rgba(0,0,0,.6);
z-index: 999;
display: none;
}
.my_model{
position: fixed;
display: none;
top:50%;
left: 50%;
margin-top: -50px;
margin-left: -200px;
z-index: 9999;
width: 360px;
height: 120px;
border: 1px solid #aeaeae;
border-radius: 3px;
padding: 20px;
background: #fff;
}
.my_model .title{
font-size: 14px;
color: #3c3c3c;
font-weight: 700;
margin-bottom: 20px;
}
.my_model .title .closeModel{
float: right;
cursor: pointer;
}
.my_model p{
line-height:16px;
}
.my_model .opBtn{
margin-top: 20px;
}
.my_model .opBtn a{
width: 58px;
height: 28px;
line-height: 28px;
text-align: center;
border-radius: 1px;
display: inline-block;
margin-right: 10px;
font-weight: 700;
}
.my_model .dialog-sure{
background: #52a0e5;
color: #fff;
border: 1px solid #52a0e5;
}
.my_model .dialog-close{
background: #fff;
border: 1px solid #d9d9d9;
color: #3c3c3c;
}

.vt-sort[data-v-2d248b32] {
  cursor: pointer;
}

.disabled svg {
  color: grey;
}
.disabled a {
  cursor: not-allowed
}
